import { Component, ViewEncapsulation } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Platform } from '@ionic/angular';
import { AlertController, LoadingController, ToastController } from '@ionic/angular';
import { Router } from '@angular/router';
import { AngularFireAuth } from '@angular/fire/auth';
import { Logger } from '../../providers/logger/logger';
import { Storage } from '@ionic/storage';



//import { UserData } from '../../providers/user-data';

import { UserOptions } from '../../interfaces/user-options';



@Component({
  selector: 'page-signup',
  templateUrl: 'signup.html',
  styleUrls: ['./signup.scss'],
})
export class SignupPage {
  email: string;
  password: string;
  username: string;
  error: string;
  image: string;

  submitted = false;

  constructor(
    public router: Router,
    public logger: Logger,
    public storage: Storage,
    private fireauth: AngularFireAuth,
    private toastController: ToastController,
    private alertController: AlertController,
  ) {}


  signupfun() {
    this.fireauth.auth.createUserWithEmailAndPassword(this.email, this.password)
      .then(res => {
        if (res.user) {
         this.logger.info(res.user);

         this.sendEmailVerification() ;
          this.updateProfile();
          this.storage.clear();
        }
      })
      .catch(err => {
        this.logger.info(err);

        this.error = err.message;
        console.log(err.message);
        //this.presentToast(err.message , false, 'bottom', 1000);
        this.presentAlert("", "", err.message) ;

      });
  }

  updateProfile() {
    this.fireauth.auth.onAuthStateChanged((user) => {
      if (user) {
        this.logger.info(user);

        user.updateProfile({
          displayName: this.username,
          photoURL: `https://picsum.photos/id/${this.image}/200/200`
        })
          .then(() => {
            this.router.navigateByUrl('/docvalidate');
          })
      }
    })
  }



  async sendEmailVerification() {
     this.fireauth.auth.currentUser.sendEmailVerification()
      .then(data => {
        this.presentToast('Email verification sent', false, 'bottom', 1000);
        this.router.navigateByUrl('/verifyemail');
      })
      .catch(err => {
        this.logger.info(err);

        this.error = err.message;
        this.presentAlert("", "", err.message) ;
      });

 }


  async presentToast(message, show_button, position, duration) {
    const toast = await this.toastController.create({
      message: message,
      showCloseButton: show_button,
      position: position,
      duration: duration
    });
    toast.present();
  }

 async presentAlert(title, subheader, message) {
    const alert = await this.alertController.create({
      header: title,
      subHeader: subheader,
      message: message,
      buttons: ['OK']
    });

    await alert.present();
   setTimeout( async ()=>{
      await alert.dismiss();
   }, 2000);

  }

  
}
